/* Fixed QR Code in Bottom Right */
.qr-fixed {
    position: fixed;
    bottom: 80px;
    right: 20px;
    width: 100px;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.95);
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    z-index: 1000;
}

.qr-fixed:hover {
    transform: scale(1.05);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

/* Silhouette background for QR code */
.qr-fixed .qr-bg {
    position: absolute;
    width: 160px;
    height: 190px;
    border-radius: 35% / 45%;
    background: rgba(44, 62, 80, 0.12);
    z-index: 0;
    left: 50%;
    top: 54%;
    transform: translate(-50%, -50%);
}

.qr-fixed img {
    width: 110px;
    height: 110px;
    object-fit: contain;
    border-radius: 12px;
    box-shadow: 0 4px 12px rgba(44,62,80,0.13);
    margin-bottom: 6px;
    background: #fff;
} 